<template>
  <div >
    <el-select v-model="option" placeholder="Select" @change="changeItem">
        <el-option
        v-for="(item, index) in options"
            :key="index"
            :label="item.value"
            :value="item.id"
        >

        </el-option>
    </el-select>
    <label slot="lable">&nbsp;&nbsp;&nbsp; </label>
    <el-button type="primary" size="small" @click="addCouseBtn()"></el-button>

    <el-dialog
        v-model="dialogVisible"
        title=""
        width="40%"
        :before-close="handleClose"
    >
        <el-form
            ref="courseInfo"
            :model="courseInfo"
            label-width="100px"
        >
            <el-form-item label="" prop="title"
                :rules="[
                    { required: true, message: ', ' },
                ]"
            >
            <el-input v-model.number="courseInfo.title" autocomplete="off"></el-input>
            </el-form-item>
            
            <el-form-item label="" prop="url">
            <el-upload
                :action="upLoadFile"   
                list-type="picture-card"   
                :file-list="fileArr"    
                :limit="1"    
                :on-success="handleAvatarSuccess"    
                >
                <el-icon><upload /></el-icon>
            
            </el-upload>
            <div slot="tip" class="el-upload__tip">, jpg/png500kb</div>

            </el-form-item>

            <el-form-item label="" prop="description"
                :rules="[
                    { required: true, message: ', ' },
                ]"
            >
            <el-input v-model.number="courseInfo.description" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item>
            <el-button type="primary" @click="addCourse('courseInfo')"></el-button>

            <el-button @click="resetForm('courseInfo')"></el-button>
            </el-form-item>
        </el-form>
        

    </el-dialog>


    <el-table :data="tableData.filter((data) => !search || data.title.toLowerCase().includes(search.toLowerCase()) || data.description.toLowerCase().includes(search.toLowerCase()))" 
        style="width: 100%">
        <el-table-column prop="title" label=""  />
        <el-table-column prop="description" label="" />
        <el-table-column prop="teacherName" label="" />
        
        <el-table-column align="right" >

            <template #header>
                    <el-input v-model="search"  size="mini" placeholder="" />
            </template>

            <template #default="scope">
                <!-- <el-button type="danger"  size="mini" @click="handleClick(scope.row.id)"></el-button> -->
                <el-popconfirm title="?" @confirm="handleClick(scope.row.id)">
                    <template #reference>
                        <el-button size="mini" type="danger"></el-button>
                    </template>
                </el-popconfirm>
                <el-button type="primary"  size="mini" @click="courseClick(scope.row.id)"></el-button>
            </template>

        </el-table-column>        
    </el-table>


    <!--  -->
    <el-dialog
        v-model="courseVisible"
        title=""
        width="60%"
        :before-close="handleClose"
    >
        <create-task :courseId="courseId" v-model="courseId"></create-task>


    </el-dialog>


  </div>
</template>

<script>
import { ref } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { Upload } from '@element-plus/icons'
import CreateTask from '../course/CreateTask'

export default {
    name: "CourseSetUp",

    setup() {
        const dialogVisible = ref(false)
        const courseVisible = ref(false)
        const handleClose = (done) => {
        ElMessageBox.confirm('?')
            .then(() => {
            done()
            })
            .catch(() => {
            // catch error
            })
        }
        return {
            dialogVisible,
            handleClose,
            courseVisible,
        }
    },

    created() {
      // 1, 
      this.$axios.get("/stu-class/getAllFormatClass").then(res => {
          
          const data = res.data
          if (data.code == 200) {
            this.options = data.data
          } else {
             ElMessage.error('', {duration: 3 * 1000})
          }
      })
    },


    data() {
        return {
            courseId: "",

            options: [],
            search: '',
            option: '',
            tableData: [],
            icon_url: "", // 

            courseInfo: {
                title: "",
                img: "",
                description: "",
                classId: "",
                teacherId: "",
            },
        }
    },

    methods: {


        resetForm(formName) {
            this.$refs[formName].resetFields()
        },

        addCouseBtn() {
            const id = this.courseInfo.classId
            if (id == undefined || id == null || id == "" ) {
                ElMessage.error('', {duration: 3 * 1000})
            } else {
                this.dialogVisible = true
            }
        },

        getCourseList(val) {
            this.$axios.get("/course/getAll?classId=" + val).then(res => {
                const data = res.data
                if (data.code == 200) {
                    this.tableData = data.data
                    ElMessage.success("", {duration: 3 * 1000})
                } else {
                    ElMessage.error('', {duration: 3 * 1000})
                }
            })
        },

        changeItem(val) {

          this.courseInfo.classId = val

          if (val == undefined || val == null || val == '') {
            ElMessage.success('', {duration: 3 * 1000})
          } else {
            this.getCourseList(val)
          }
        },
        handleClick(id) {
            // 
            this.$axios.post("/course/delete?courseId=" + id).then(res => {
                const data = res.data
                if (data.code == 200 && data.data == true) {
                    ElMessage.success("", {duration: 3 * 1000})
                    const classId = this.courseInfo.classId
                    if (classId != null ) {
                        // 
                        this.getCourseList(classId)
                    }

                }else {
                    ElMessage.error('', {duration: 3 * 1000})
                }

            })
        },

        handleAvatarSuccess(res) {
            // 
            if (res.code === 200) {
                this.icon_url = this.$downLoad + res.data.fileName
                this.courseInfo.img = res.data.fileName
                ElMessage.success("", {duration: 3 * 1000})
            } else {
                ElMessage.error('', {duration: 3 * 1000})
            }
       },

       // 
       addCourse(formName) {
           this.$refs[formName].validate((valid) => {
                
                if (valid) {

                    const userinfo = this.$store.getters.getUser
                    this.courseInfo.teacherId = userinfo.id
                    
                    this.$axios.post("/course/add", this.courseInfo).then(res => {
                        const data = res.data
                        if (data.code == 200 && data.data == true) {
                            ElMessage.success("", {duration: 3 * 1000})
                            const classId = this.courseInfo.classId
                            if (classId != null ) {
                                // 
                                this.getCourseList(classId)
                                this.resetForm('courseInfo')
                            }
                        }else {
                            ElMessage.error('', {duration: 3 * 1000})
                        }

                    })
                    this.dialogVisible = false
                } else {
                    return false
                }
            })
       },

       courseClick(id) {
           this.courseVisible = true
           this.courseId = id
       }

    },
    computed: {
        
        fileArr() {
            //  
            return this.icon_url ? [{ url: this.icon_url }] : []
        },

        upLoadFile() {
            return this.$updLoad
        }
    },

    components: {
        Upload,
        CreateTask,
    }

}
</script>

