<template>
  
  <div>

    <el-tabs :tab-position="'left'">
            <el-tab-pane label="">

            <div>
                <el-table  
                :data="classList.filter((data) => !search || data.major.toLowerCase().includes(search.toLowerCase()) || data.grade.toLowerCase().includes(search.toLowerCase())) "
            style="width: 100%">

                <el-table-column prop="grade" label="" />
                <el-table-column prop="major" label="" />

                <el-table-column align="right" >
                    
                    <template #header>
                         <el-input v-model="search"  size="mini" placeholder="" />
                    </template>

                    <template #default="scope">
                        <el-popconfirm title=",?" @confirm="deleteClass(scope.row.id)">
                            <template #reference>
                                <el-button size="mini" type="danger"></el-button>
                            </template>
                        </el-popconfirm>

                        <!-- <el-button type="danger"  size="mini" @click="deleteClass(scope.row.id)"></el-button> -->
                        <el-button type="primary" size="mini" @click="showDialog(scope.row.id)"></el-button>

                        <!--  -->
                        <el-dialog
                            v-model="dialogVisible"
                            title=""
                            width="40%"
                            :before-close="handleClose"
                            center
                            :destroy-on-close="true"
                        >
                            
                            <el-tabs type="border-card" >
                                <el-tab-pane label="">
                                    
                                    <div style="text-align: center"> 
                                    <el-upload
                                        drag
                                        action=""
                                        :http-request="uploadFile"
                                        :data="{ 'id': addStuClassId }"
                                    >
                                        <el-icon class="el-icon--upload"><upload-filled /></el-icon>
                                        <div class="el-upload__text">
                                         <em></em>
                                        </div>

                                        <template #tip>
                                        <div class="el-upload__tip">
                                            csv
                                        </div>
                                        </template>
                                    </el-upload>
                                    <el-button type="primary" @click="downloadAttachRow"></el-button>
                                    </div>
                                </el-tab-pane>
                                <el-tab-pane label="">
                                    <el-form
                                        ref="stuInfo"
                                        :model="stuInfo"
                                        label-width="100px"
                                    >
                                    <el-form-item label="" prop="name"
                                        :rules="[
                                            { required: true, message: ', ' },
                                        ]"
                                    >
                                    <el-input v-model.number="stuInfo.name" autocomplete="off"></el-input>
                                    </el-form-item>
                                    <el-form-item label="" prop="number"
                                        :rules="[
                                            { required: true, message: ', 180405218' },
                                        ]"
                                    >
                                    <el-input v-model.number="stuInfo.number" autocomplete="off"></el-input>
                                    </el-form-item>
                                    <el-form-item label="" prop="password"
                                        :rules="[
                                            { required: true, message: ', 123456' },
                                        ]"
                                    >
                                    <el-input v-model.number="stuInfo.password" autocomplete="off"></el-input>
                                    </el-form-item>

                                    <el-form-item label="" prop="classNumber"
                                        :rules="[
                                            { required: true, message: ', 1' },
                                        ]"
                                    >
                                    <el-input v-model.number="stuInfo.classNumber" autocomplete="off"></el-input>
                                    </el-form-item>

                                    <el-form-item>
                                    <el-button type="primary" @click="addStudent('stuInfo')"></el-button>

                                    <el-button @click="resetForm('stuInfo')"></el-button>
                                    </el-form-item>
                                    </el-form>
                                </el-tab-pane>
                            </el-tabs>

                        </el-dialog>


                    </template>
                </el-table-column>
        
                </el-table>
            </div>
            
        </el-tab-pane>

        <el-tab-pane label="">
            
            <el-form
                ref="stuClass"
                :model="stuClass"
                label-width="100px"
            >
            <el-form-item label="" prop="grade"
                :rules="[
                    { required: true, message: ', 2018' },
                ]"
            >
            <el-input v-model.number="stuClass.grade" autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item label="" prop="major"
                :rules="[
                    { required: true, message: ', ' },
                ]"
            >
            <el-input v-model.number="stuClass.major" autocomplete="off"></el-input>
            </el-form-item>


            <el-form-item>
            <el-button type="primary" @click="submitForm('stuClass', 'scope.row.id')"></el-button>

            <el-button @click="resetForm('stuClass')"></el-button>
            </el-form-item>

            </el-form>
        </el-tab-pane>
    </el-tabs>

    
  </div>


</template>

<script>
import { ElMessage , ElMessageBox } from 'element-plus'
import { ref } from 'vue'
import { UploadFilled } from '@element-plus/icons'

export default {
    name: "ClassSetUp",

    setup() {
        const dialogVisible = ref(false)

        const handleClose = (done) => {
        ElMessageBox.confirm('?')
            .then(() => {
                done()
            })
            .catch(() => {
            // catch error
            })
        }
        return {
            dialogVisible,
            handleClose,
        }
  },

    created() {
        //  
        this.init()

    },
    data() {
        return {

            // 
            stuClass: {
                major: "",
                grade: "",

            },

            stuInfo: {
                name: "",
                number: "",
                password: "",
                classNumber: "",
            },

            classList: [],
            search: '',
            
            addStuClassId: "",

        }
    },

    methods: {

        init() {
            this.$axios.get("/stu-class/getAll").then(res => {
                this.classList = res.data.data
            });
        },
        
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {

                if (valid) {
                    // 1 
                    this.$axios.post("/stu-class/addClass", {

                        "major": this.stuClass.major,
                        "grade": this.stuClass.grade

                    }).then(res => {
                        const data = res.data
                        if (data.code == 200) {
                            ElMessage.success('', {duration: 3 * 1000})
                            this.stuClass.major = "",
                            this.stuClass.grade = ""
                            this.init()
                        }

                    })
                    
                } else {
                    return false
                }
            })
        },

        resetForm(formName) {
            this.$refs[formName].resetFields()
        },

        // 
        deleteClass(id) {
            // 1 
            this.$axios.post("/stu-class/delete?id=" + id).then(res => {
                const data = res.data
                if (data.code == 200) {
                    ElMessage.success('', {duration: 3 * 1000})
                    // 2 
                    this.init()
                }
            })

        },

        addStudent(formName) {
            this.$refs[formName].validate((valid) => {
                

                if (valid) {
                    // 1, 
                    this.$axios.post("/user/addStu", {
                        "username": this.stuInfo.number, // 
                        "name": this.stuInfo.name,   // 
                        "password": this.stuInfo.password,
                        "type": 2,
                        "classId": this.addStuClassId,
                        "classNumber": this.stuInfo.classNumber,

                    }).then(res => {
                        const data = res.data
                        if (data.code == 200) {
                            ElMessage.success('', {duration: 3 * 1000})
                            this.stuInfo.number = ""
                            this.stuInfo.name = ""
                            this.stuInfo.password = ""
                            this.stuInfo.classNumber = ""
                        }
                    })
                } else {
                    return false
                }
            })
            
        },
        uploadFile(params) {
            
            let formData = new FormData()
            formData.append("file", params.file)
            formData.append("id", params.data.id)

            this.$axios({
                url: "/user/upload",
                method: 'post',
                data: formData,
                headers: { 'Content-Type': 'multipart/form-data'}
            }).then(res=> {
                
                const data = res.data
                if (data.code == 200) {
                    ElMessage.success('', {duration: 3 * 1000})
                    location.reload()
                } else {
                    return false
                }
            })
            
        },
        /**  */
        downloadAttachRow() {
            window.open(this.$downLoad + 'file.csv', '_blank')
        },

        showDialog(val) {
            this.dialogVisible = true 
            this.addStuClassId = val
        }

    },
    components: {
        UploadFilled,
    }
    
}
</script>

