<template>
  <div>
    <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="" name="first">
      <el-table :data="tableData" style="width: 100%">
        
        <el-table-column prop="taskName" label=""  />
        <el-table-column prop="viewPermission" label=""  />
        <el-table-column prop="scorePermission" label="" />
        <el-table-column prop="startTime" label=""  />
        <el-table-column prop="stopTime" label=""  />
        <el-table-column prop="weight" label="" />

      <el-table-column>
        <template #default="scope">
          <!-- <el-button type="danger"  size="mini" @click="deleteTask(scope.row.id)"></el-button> -->

          <el-popconfirm title="?" @confirm="deleteTask(scope.row.id)">
            <template #reference>
              <el-button size="mini" type="danger"> </el-button>
            </template>
          </el-popconfirm>

        </template>
      </el-table-column>
      </el-table>
    </el-tab-pane>
    
    <el-tab-pane label="" name="second">
      
      <el-form ref="taskInfo" :model="taskInfo" label-width="100px">
        <el-form-item label="" prop="taskName" :rules="[{ required: true, message: ', ' },]">
          <el-input v-model.number="taskInfo.taskName" autocomplete="off"></el-input>
        </el-form-item>

      <el-form-item label="" prop="description" :rules="[{ required: true, message: ', c++' },]">
        <el-input v-model.number="taskInfo.description" autocomplete="off"  :autosize="{ minRows: 3, maxRows: 5 }" type="textarea"></el-input>
      </el-form-item>


      <el-form-item label="" prop="weight" :rules="[{ required: true, message: ',  0  1 ' },]">
        <el-input-number v-model.number="taskInfo.weight" :precision="2" :step="0.1" :max="1" />
        <label slot="lable">&nbsp;&nbsp;&nbsp; </label>
        <el-tooltip
          effect="dark"
          content=", "
          placement="right-start"
        >
        <el-button></el-button>
        </el-tooltip>
      </el-form-item>

      <el-form-item label="">  
        <el-switch v-model="viewPermission" ></el-switch>
        <label slot="lable">&nbsp;&nbsp;&nbsp; </label>
        <el-tooltip
          effect="dark"
          content=","
          placement="right-start"
        >
        <el-button></el-button>
      </el-tooltip>

      </el-form-item>

      <el-form-item label="">  
        <el-switch v-model="scorePermission" ></el-switch>
        <label slot="lable">&nbsp;&nbsp;&nbsp; </label>
        <el-tooltip
          effect="dark"
          content=",,"
          placement="right-start"
        >
        <el-button></el-button>
        </el-tooltip>
      </el-form-item>

      <el-form-item label="">

        <el-date-picker
          v-model="commitTime"
          type="datetimerange"
          format="YYYY-MM-DD HH:mm:ss"
          value-format="YYYY-MM-DD HH:mm:ss"
          start-placeholder=""
          end-placeholder=""
        >
        </el-date-picker>
      </el-form-item>
      
      <el-form-item>
      <el-button type="primary" @click="submitForm('taskInfo')"></el-button>

      <el-button @click="resetForm('taskInfo')"></el-button>
      </el-form-item>
      </el-form>

    </el-tab-pane>

    <el-tab-pane label="" name="third">
      <div>
        <el-select v-model="option" placeholder="Select" @change="changeItem">
          <el-option
          v-for="(item, index) in tableData"
              :key="index"
              :label="item.taskName"
              :value="item.id"
          >
          </el-option>
        </el-select>
        <br>
        <br>

        <el-form ref="taskInfo" :model="taskInfo" label-width="100px">

        <el-form-item label="" prop="taskName" :rules="[{ required: true, message: ', ' },]">
          <el-input v-model.number="taskInfo.taskName" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" prop="description" :rules="[{ required: true, message: ', c++' },]">
          <el-input v-model.number="taskInfo.description" autocomplete="off"  :autosize="{ minRows: 3, maxRows: 5 }" type="textarea"></el-input>
        </el-form-item>

        <el-form-item label="" prop="weight" :rules="[{ required: true, message: ',  0  1 ' },]">
          <el-input-number v-model.number="taskInfo.weight" :precision="2" :step="0.1" :max="1" />
          <label slot="lable">&nbsp;&nbsp;&nbsp; </label>
        <el-tooltip
          effect="dark"
          content=", "
          placement="right-start"
        >
        <el-button></el-button>
        </el-tooltip>
       </el-form-item>

        <el-form-item label="">  
          <el-switch v-model="viewPermission" ></el-switch>
          <label slot="lable">&nbsp;&nbsp;&nbsp; </label>
          <el-tooltip
            effect="dark"
            content=","
            placement="right-start"
          >
             <el-button></el-button>
          </el-tooltip>
        </el-form-item>

        <el-form-item label="">  
          <el-switch v-model="scorePermission" ></el-switch>
          <label slot="lable">&nbsp;&nbsp;&nbsp; </label>
          <el-tooltip
              effect="dark"
              content=",,"
              placement="right-start"
            >
            <el-button></el-button>
          </el-tooltip>
        </el-form-item>

        <el-form-item label="">

          <el-date-picker
            v-model="commitTime"
            type="datetimerange"
            format="YYYY-MM-DD HH:mm:ss"
            value-format="YYYY-MM-DD HH:mm:ss"
            start-placeholder=""
            end-placeholder=""
          >
          </el-date-picker>
        </el-form-item>
      
        <el-form-item>
        <el-button type="primary" @click="updateTask"></el-button>
        <el-button @click="resetForm('taskInfo')"></el-button>
        </el-form-item>
        </el-form>
      </div>

    </el-tab-pane>
    </el-tabs>


  </div>
</template>

<script>
import { ElMessage } from "element-plus"

export default {
    computed: {
      ElMessage,
    },

    name: "CreateTask",
    props: ['courseId'],
    
    created() {
      this.taskInfo.courseId = this.courseId
      this.init()
    },

    data() {
      return {

        // 
        option: '',
        taskId: "",

        activeName: 'first',

        // 
        tableData: [],

        // 
        commitTime: '',
        viewPermission: false,
        scorePermission: false,

        taskInfo: {
          courseId: "",
          taskName: "",
          description: "",
          viewPermission: "",
          scorePermission: "",
          startTime: "",
          stopTime: "",
          weight: 0.0,
        },
        

      }
    },
    methods: {
      
      // 
      resetForm(formName) {
        this.$refs[formName].resetFields()
        this.viewPermission = false
        this.scorePermission = false
        this.taskInfo.weight = 0.0
      },
      setParam() {
        this.taskInfo.startTime = this.commitTime[0]
        this.taskInfo.stopTime = this.commitTime[1]
        this.taskInfo.viewPermission = this.viewPermission? 1: 0
        this.taskInfo.scorePermission = this.scorePermission? 1: 0
      },
      // 
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          
          const len = this.commitTime.length
          // console.log(len)
          if (len < 2) {
            ElMessage.error("", {duration: 3 * 1000})
            return
          }
          if (valid) {
            // 
            this.setParam()
            this.$axios.post("/course-task/addTask", this.taskInfo).then(res => {
              const data = res.data
              if (data.code == 200) {
                ElMessage.success("", {duration: 3 * 1000})
                this.resetForm('taskInfo')
                this.init()
                this.commitTime=''
                this.viewPermission = false
                this.scorePermission = false

              } else {
                ElMessage.error("", {duration: 3 * 1000})
              }
            })
          }
        })
      },
      // 
      updateTask() {
        this.$refs['taskInfo'].validate((valid) => {
          const len = this.commitTime.length
          // console.log(len)
          if (len < 2) {
            ElMessage.error("", {duration: 3 * 1000})
            return
          }
          // 
          if (valid) {
            // 0, 
            this.setParam()
            // 1, 
            let updateData = JSON.parse(JSON.stringify(this.taskInfo))
            updateData.id = this.taskId

            // console.log(updateData)

            this.$axios.post("/course-task/updateTask", updateData).then(res => {
              const data = res.data
              if (data.code == 200) {
                ElMessage.success("", {duration: 3 * 1000})
                this.init()
                this.resetForm('taskInfo')
              } else {
                ElMessage.error("", {duration: 3 * 1000})
              }
            })

          } else {
             ElMessage.error("", {duration: 3 * 1000})
          }
        })
      },
      handleClick() {
        // tab, event
      },

      init() {

        this.$axios.get("/course-task/getAll?courseId="+ this.taskInfo.courseId).then(res => {
          const data = res.data
          if (data.code == 200) {
            this.tableData = data.data
          }
        })

      },

      deleteTask(id) {
        // 1, 
        this.$axios.post("/course-task/deleteTask?taskId=" + id).then(res => {
          const data = res.data
          if (data.code == 200) {
            ElMessage.success("", {duration: 3 * 1000})
            this.init()
          } else {
            ElMessage.error("", {duration: 3 * 1000})
          }

        })
        // 2 
        this.init()
      },

      // select 
      changeItem(val) {
          this.taskId = val
          if (val == undefined || val == null || val == '') {
            ElMessage.success('', {duration: 3 * 1000})
          } else {
            //   
            const data = this.tableData.find(x => x.id == val)
            this.taskInfo.taskName = data.taskName
            this.taskInfo.description = data.description
            this.viewPermission = data.viewPermission == 1? true: false
            this.scorePermission = data.scorePermission == 1? true: false
            this.commitTime = [data.startTime, data.stopTime]
          }
      },
      
    },

    watch: {
      'courseId': function(val) {
        this.taskInfo.courseId = val
        this.init()
      }
    }

}
</script>

